import wrap from '@/components/wrap';
import Dialog from '../../wxcomponents/vant/dialog/dialog';
const message = '哈哈';
export default {
  data() {
    return {
      show: false,
      username: '',
      password: '',
    };
  },
  methods: {
    showCustomDialog() {
      this.show = true;
    },

    onClickAlert() {
      Dialog.alert({
        title: '标题',
        message,
      });
    },

    getUserInfo(event) {
      console.log(event.detail);
    },

    onClickAlert2() {
      Dialog.alert({
        message,
      });
    },

    onClickConfirm() {
      Dialog.confirm({
        title: '标题',
        message,
      })
        .then(() => {
          console.log('confirm');
        })
        .catch(() => {
          console.log('cancel');
        });
    },

    onClose(event) {
      if (event.detail === 'confirm') {
        setTimeout(() => {
          this.show = false;
        }, 1000);
      } else {
        this.show = false;
      }
    },
  },
  components: {
    wrap,
  },
};
